// Side navigation
$vl-invisible: rgba($vl-black, 0) !default;
$vl-seethrough: rgba($vl-black, .3) !default;


.vl-side-navigation {
  max-height: 90vh;
  overflow: auto;
  padding: 5px; // because overflow will cut off focus state outline

  @include respond-to(medium-up) {
    // sass-lint:disable no-vendor-prefixes
    &::-webkit-scrollbar {
      width: 16px;
      height: 20px;
    }

    &::-webkit-scrollbar-thumb {
      height: 8px;
      border: 6px solid $vl-invisible;
      border-radius: 7px;
      background-clip: padding-box;
      background-color: $vl-athens-gray;
    }

    &::-webkit-scrollbar-button {
      display: none;
      width: 0;
      height: 0;
    }

    &::-webkit-scrollbar-corner {
      background-color: transparent;
    }
  }

  @include respond-to(small) {
    display: none;
    min-height: 10rem;
    max-height: calc(100vh - 15rem);
    padding: 1.5rem 1rem 1.5rem 1.5rem;
    background: $vl-page-bg;
    box-shadow: 0 0 2.1rem $vl-seethrough;
    animation: fade-transition 0.3s;
  }

  a {
    text-decoration: none;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }

  // Mobile navigation active state
  .js-vl-scrollspy-mobile--active & {
    display: block !important; // sass-lint:disable-line no-important
  }

  .js-vl-scrollspy-active {
    color: $vl-text-color;
  }

  &[data-vl-side-navigation-scrollable] {
    @include respond-to(medium-up) {
      max-height: none;
    }
  }
}

.vl-side-navigation__content {
  font-size: 1.6rem;

  @include respond-to(small) {
    font-size: $vl-base-mobile-font-size;
  }
}

// seperate groups of links in the navigation
.vl-side-navigation__group {
  margin-bottom: 2rem;

  @include respond-to(small) {
    padding: 0;
    border: 0 !important; // sass-lint:disable-line no-important
  }

  &:last-child {
    margin-bottom: 0;
  }

  &--has-title {
    padding-top: 3rem;
    border-top-width: 3px;

    @include respond-to(small) {
      padding-top: 1.5rem;
    }
  }
}

.vl-side-navigation__title {
  margin-bottom: 20px;
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: $vl-bluey-gray;
  font-weight: 500;
}

.vl-side-navigation__item {

  @include respond-to(small) {
    padding: 0;
    font-size: 1.4rem;
  }

  .vl-side-navigation__group--spaced &:not(:last-child) {
    margin-bottom: $vl-spacing-small;
  }

  .vl-side-navigation__group--has-title & + & {
    margin-top: 0.5rem;
  }

  > a {
    @include respond-to(small) {
      display: block;
      margin: .7rem 0 1.4rem;
    }
  }

  a {
    font-weight: 500;
  }

  &:last-child {
    > a {
      @include respond-to(small) {
        margin-bottom: .7rem;
      }
    }
  }

  // Level two list
  > ul {
    display: none;
    padding: 0 0 .15rem 2rem;

    @include respond-to(small) {
      padding: 0 1rem .15rem 1.3rem;
    }

    &.vl-side-navigation__subgroup--active {
      display: block;
    }
  }
}

.vl-side-navigation__toggle {
  display: block;
  position: relative;
  font-weight: 500;
  box-shadow: none;
  margin: 13px 0;

  &[aria-expanded="true"] {
    .vl-vi {
      &::before {
        transform: rotate(90deg);
      }
    }

    + ul {
      display: block;
      animation: side-navigation-transition 0.3s;

      .vl-side-navigation__toggle {
        &:first-child {
          margin-top: 0;
        }
      }
    }
  }

  .vl-vi {
    font-size: 1.6rem;
    position: absolute;
    top: 50%;
    right: 0.4rem;
    transform: translateY(-50%);

    &::before {
      transition: transform 0.1s ease-in-out;
    }
  }
}

@keyframes side-navigation-transition {
  0% {
    display: none;
    transform: translateX(-10px);
  }

  1% {
    display: block;
    transform: translateX(-10px);
  }

  100% {
    display: block;
    transform: translateX(0);
    opacity: 1;
  }
}

@import "modules/scrollspy";
@import "modules/sticky";
