// IMPORTANT: we can not namespace .tooltip__inner,
// this is needed for the tooltip.js/popper.js library

$vl-tooltip-arrow-width: 0.5rem;
$vl-tooltip-arrow-outer-width: ($vl-tooltip-arrow-width + 0.1rem);
$vl-tooltip-translate: ($vl-tooltip-arrow-width + 0.3rem);
$vl-tooltip-shadow: 0 0 2rem 0 rgba($vl-text-color, 0.15);

.vl-tooltip {
  max-width: 27rem;
  background: $vl-page-bg;
  border: 1px $vl-input-border-color-alt solid;
  text-align: center;
  font-size: $vl-base-font-size--small;
  line-height: 1.25;
  font-weight: 400;
  color: $vl-text-color;
  font-family: $vl-base-font;
  padding: .3rem 1rem;
  z-index: 1;
  position: relative;
  box-shadow: $vl-tooltip-shadow;

  //to catch the mouse when it leaves the trigger element
  &::before {
    content: "";
    position: absolute;
    top: - ($vl-tooltip-translate + 0.1rem);
    right: - ($vl-tooltip-translate + 0.1rem);
    bottom: - ($vl-tooltip-translate + 0.1rem);
    left: - ($vl-tooltip-translate + 0.1rem);
  }

  &--static {
    display: block;
    pointer-events: auto;

    &[x-placement^="top"] {
      transform: translate(-50%, -100%);
      margin-left: 50%;
    }

    &[x-placement^="left"] {
      transform: translate(-100%, 0%);
    }

    &[x-placement^="bottom"] {
      transform: translate(-50%, 0%);
      margin-left: 50%;
      top: 100%;
    }

    &[x-placement^="right"] {
      margin-left: calc(100% + 10px);
    }
  }

  //When charcount is higher than 110 align text to the left + lower fontweight
  &.vl-tooltip--large {
    font-weight: 400;
    font-size: 1.4rem;
    padding: 1rem;

    .tooltip__inner {
      text-align: left;
    }
  }

  &__title {
    margin: 0 0 .5rem;
    border-bottom: 1px solid darken($vl-input-border-color-alt, 6%);
    font-size: 1.6rem;
  }

  &__close {
    border: 0;
    font-size: 2.2rem;
    position: absolute;
    top: 0;
    right: 0;
    background: transparent;
    padding: .3rem 1.2rem;
    z-index: 1061;
    cursor: pointer;
  }

  &[x-placement^="top"] {
    .vl-tooltip__arrow {
      left: 50%;
      margin-left: -$vl-tooltip-arrow-outer-width;
      border-bottom-width: 0;
      border-top-color: $vl-input-border-color-alt;
      bottom: -$vl-tooltip-arrow-outer-width;

      &::after {
        content: " ";
        bottom: 1px;
        margin-left: -$vl-tooltip-arrow-width;
        border-bottom-width: 0;
        border-top-color: $vl-page-bg;
      }
    }
  }

  &[x-placement^="right"] {
    .vl-tooltip__arrow {
      top: 50%;
      left: -$vl-tooltip-arrow-outer-width;
      margin-top: -$vl-tooltip-arrow-outer-width;
      border-left-width: 0;
      border-right-color: $vl-input-border-color-alt;

      &::after {
        content: " ";
        left: 1px;
        bottom: -$vl-tooltip-arrow-width;
        border-left-width: 0;
        border-right-color: $vl-page-bg;
      }
    }
  }

  &[x-placement^="bottom"] {
    .vl-tooltip__arrow {
      left: 50%;
      margin-left: -$vl-tooltip-arrow-outer-width;
      border-top-width: 0;
      border-bottom-color: $vl-input-border-color-alt;
      top: -$vl-tooltip-arrow-outer-width;

      &::after {
        content: " ";
        top: 1px;
        margin-left: -$vl-tooltip-arrow-width;
        border-top-width: 0;
        border-bottom-color: $vl-page-bg;
      }
    }
  }

  &[x-placement^="left"] {
    .vl-tooltip__arrow {
      top: 50%;
      right: -$vl-tooltip-arrow-outer-width;
      margin-top: -$vl-tooltip-arrow-outer-width;
      border-right-width: 0;
      border-left-color: $vl-input-border-color-alt;

      &::after {
        content: " ";
        right: 1px;
        border-right-width: 0;
        border-left-color: $vl-page-bg;
        bottom: -$vl-tooltip-arrow-width;
      }
    }
  }
}

// Arrows
//
.vl-tooltip__arrow {
  border-width: $vl-tooltip-arrow-outer-width;

  &,
  &::after {
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
    border-width: $vl-tooltip-arrow-width;
    content: "";
  }
}
