.vl-agenda-item {
  position: relative;
  display: block;
  background-color: $vl-alt-bg;
  color: $vl-text-color;
  text-decoration: none;
  min-height: 8rem;
  padding: 1.5rem 1.5rem 1.5rem 8rem;

  @include respond-to(small) {
    height: auto;
    min-height: 7rem;
  }

  &[href] {
    &:hover,
    &:focus {
      background-color: $vl-alt-hover-bg;

      .vl-agenda-item__title {
        text-decoration: underline;
        color: $vl-action-text-hover-color;
      }

      .vl-agenda-item__date {
        background-color: $vl-action-hover-color;
      }
    }
  }

  &[href] & {
    &__title {
      color: $vl-action-text-color;
    }
  }

  // Agenda item date
  &__date {
    position: absolute;
    display: block;
    background-color: $vl-action-color;
    color: $vl-page-bg;
    width: 5rem;
    text-align: center;
    padding: 1.5rem 1rem;
    margin: 0;
    top: 0;
    bottom: 0;
    left: 0;

    .vl-agenda-item--no-link & {
      background-color: $vl-success-color;
      color: $vl-page-bg;
    }

    // Agenda item date day
    &__day {
      display: block;
      font-weight: 500;
    }

    // Agenda item date month
    &__month {
      display: block;
      font-size: 1.4rem;
      overflow: hidden;

      @include respond-to(small) {
        font-size: 1.2rem;
      }
    }
  }

  // Agenda item title
  &__title {
    @include vl-u-text--ellipse;
    font-weight: 500;
    line-height: 1.2;
    margin-bottom: 0.5rem;
    color: $vl-text-color;

    &[href],
    a {
      color: $vl-action-text-color;
      text-decoration: none;

      &:hover,
      &:focus {
        text-decoration: underline;
      }
    }
  }

  // Agenda item text / note
  &__text,
  &__note {
    font-size: 1.6rem;
    line-height: 1.3;
    color: $vl-text-color;

    @include respond-to(small) {
      font-size: $vl-base-mobile-font-size;
    }
  }

  // Agenda item text
  &__text {
    max-height: 4.3rem;
    overflow: hidden;
  }

  // Agenda item note
  &__note {
    @include vl-u-text--ellipse;
  }
}
