$vl-block-padding: 1.5rem;
$vl-custom-easing: cubic-bezier(0, 0.1, 0, 0.9);

// Hero navigation
.vl-hero-navigation {
  position: relative;
  background: $vl-alt-bg;
  min-height: 56rem;

  @include respond-to(small) {
    @include clearfix;
    height: auto;
    min-height: 0;
  }

  // Hero navigation background
  &__background {
    position: absolute;
    background-size: cover !important; // sass-lint:disable-line no-important
    width: 100%;
    height: 100%;
    opacity: 0;
    will-change: transform;
    transition: all 0.2s ease-in-out;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;

    @include respond-to(small) {
      display: none;
    }

    // Hero navigation background active modifier
    &--active {
      opacity: 1;
    }
  }

  // Hero navigation content
  &__content {
    position: absolute;
    text-align: center;
    right: 0;
    bottom: 8%;
    left: 0;
    z-index: 2;

    @include respond-to(medium) {
      margin: 3.5rem 0 0;
    }

    @include respond-to(small) {
      position: relative;
      margin: 0;
      bottom: auto;
    }
  }

  // Hero navigation list
  &__list {
    display: flex;
    justify-content: center;

    @include respond-to(medium) {
      display: block;
    }

    // Hero navigation list listitem
    &__listitem {
      display: flex;
      width: 23rem;
      text-align: left;
      will-change: transform;
      transform: translateZ(0) scale(0.8);
      transition: all 0.2s $vl-custom-easing;

      @include respond-to(medium) {
        display: inline-block;
        width: 25rem;
        vertical-align: middle;
        transform: none;
        margin: 2rem;
      }

      @include respond-to(small) {
        display: block;
        background-color: $vl-alt-bg;
        background-size: cover;
        width: auto;
        max-width: none;
        transform: none;
        padding: 4rem 1rem;
        margin: 1rem;
        margin-bottom: 1rem;

        &:last-child {
          margin-bottom: 0;
        }
      }

      // Hero navigation list listitem active modifier
      &--active {
        transform: scale(1);

        .vl-hero-navigation__title {
          text-decoration: underline;
        }
      }
    }
  }

  // Hero navigation block
  &__block {
    @include clearfix;
    position: relative;
    display: block;
    background: $vl-page-bg;
    color: $vl-text-color;
    text-decoration: none;
    border-top: 0.3rem solid $vl-action-color;
    padding: ($vl-block-padding - 0.3rem) $vl-block-padding ($vl-block-padding + 1rem);

    @include respond-to(small) {
      max-width: 75%;
      padding: ($vl-block-padding - 0.3rem) $vl-block-padding $vl-block-padding;
      margin: 0 auto;
    }
  }

  // Hero navigation title
  &__title {
    @include vi;
    @include vi-u-s;
    @include vi-arrow;
    position: relative;
    font-size: 2rem;
    font-weight: 500;
    line-height: 1.2;
    color: $vl-action-text-color;
    margin: 0 0 $vl-block-padding;

    &::before {
      display: none;
    }

    @include respond-to(medium) {
      min-height: 4rem;
      padding-right: 2rem;
      margin: 1.2rem 0 0;

      &::before {
        position: absolute;
        display: block;
        color: $vl-text-color;
        width: 0.7rem;
        height: 1.1rem;
        top: 0.6rem;
        right: 0.5rem;
      }
    }

    @include respond-to(small) {
      font-size: 1.8rem;
    }
  }

  // Hero navigation description
  &__description {
    font-size: 1.5rem;
    line-height: 1.2;
    margin: 0 0 2.5rem;

    @include respond-to(medium) {
      display: none;
    }
  }

  // Hero navigation cta
  &__cta {
    position: absolute;
    bottom: $vl-block-padding;
    left: $vl-block-padding;
    right: $vl-block-padding;

    .vl-link__icon {
      margin-left: auto;
    }

    @include respond-to(medium) {
      display: none;
    }
  }
}
