/**
 * Webcomponents design refresh
 * This is still in alpha phase, don't touch & don't use!
*/

.vl-doormat {
  display: flex;
  position: relative;
  padding: $vl-spacing-small 2rem;
  background-color: $vl-alt-bg;
  color: $vl-text-color;
  text-decoration: none;
  border: 1px $vl-border-color solid;
  border-radius: 3px;

  &::before {
    content: "";
    position: absolute;
    top: -1px;
    left: -1px;
    bottom: -1px;
    background-color: $vl-border-color;
    width: 0.5rem;
    height: calc(100% + 2px);
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }

  &--alt,
  .vl-region--alt & {
    background-color: $vl-page-bg;
  }

  &--alt {
    .vl-region--alt & {
      background-color: $vl-alt-bg;
    }
  }

  &--graphic {
    flex-direction: column;

    .vl-doormat__graphic-wrapper {
      display: block;
      position: relative;
      margin-top: -#{$vl-spacing-small + 0.1rem};
      margin-right: -#{$vl-spacing-normal + 0.1rem};
      margin-bottom: #{$vl-spacing-small + 0.1rem};
      margin-left: -#{$vl-spacing-small + 0.1rem};
      border-top-right-radius: 3px;
      overflow: hidden;

      .vl-doormat__graphic {
        width: 100%;
        display: block;
        transition: transform 0.2s;
      }
    }

    &:hover,
    &:focus {
      .vl-doormat__graphic {
        transform: scale(1.05);
      }
    }
  }

  &[href] {
    &::before {
      background-color: $vl-action-color;
      transition: background-color 0.2s;
    }

    &:hover,
    &:focus {
      color: $vl-text-color;
      .vl-doormat__title {
        text-decoration: underline;
      }
    }

    &:hover {
      &::before {
        background-color: $vl-action-hover-color;
      }

      .vl-doormat__title {
        color: $vl-action-text-hover-color;
      }
    }

    &:focus {
      &::before {
        background-color: $vl-action-active-color;
      }

      .vl-doormat__title {
        color: $vl-action-text-active-color;
      }
    }
  }

  &__title {
    @include title(5);
    color: $vl-action-text-color;
    margin-bottom: $vl-spacing-xsmall;

    @include respond-to(small) {
      margin-bottom: $vl-spacing-xsmall;
    }
  }

  &__text,
  &__content {
    font-size: $vl-base-font-size;
    line-height: $vl-base-line-height;

    @include respond-to(small) {
      font-size: $vl-base-mobile-font-size;
      line-height: $vl-base-mobile-line-height;
    }
  }

  &__image {
    max-width: 15rem;
    float: right;
    flex-shrink: 0;
    margin-left: $vl-spacing-small;
  }
}
