.vl-link-list {
  &__item {
    font-size: $vl-base-font-size;
    margin-bottom: 1rem;
    padding-bottom: 1rem;

    &:not(:last-of-type) {
      margin-bottom: 0.5rem;
    }

    @include respond-to(small) {
      font-size: $vl-base-mobile-font-size;
    }
  }

  &--bordered & {
    &__item {
      margin-bottom: 1rem;
      padding-bottom: 1rem;
      border-bottom: 1px solid $vl-border-alt-color;

      .vl-region--alt & {
        border-bottom-color: darken($vl-border-alt-color, 10%);
      }

      &--no-border {
        border-bottom: 0;
      }
    }
  }

  &--bordered-alt & {
    &__item {
      margin-bottom: 1rem;
      padding-bottom: 1rem;
      position: relative;
      border-bottom-width: 1px;
      border-bottom-style: solid;
      border-image: linear-gradient(90deg, $vl-border-alt-color 0, $vl-border-alt-color 40px, transparent 41px, transparent 100%);
      border-image-slice: 1;

      .vl-region--alt & {
        border-bottom-color: darken($vl-border-alt-color, 10%);
      }

      &--no-border {
        border-bottom: 0;
      }
    }
  }

  &--small & {
    &__item {
      font-size: $vl-base-font-size--small;
    }
  }

  &--inline {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 0 #{-$vl-link-list-spacing / 2};

    .vl-link-list__item {
      padding: 0 #{$vl-link-list-spacing / 2};
      margin-bottom: 1rem;
    }
  }
}
